.services-page {
  margin: 1rem auto;

  .super-body.section-margin {
      margin: 6rem 0 3rem;
  }


  strong, b {
      color: $primary;
  }

  h2 {
    font-family: $avBlack;
    font-size: 2rem;
    color: $darkGrey;
  }

  // ********** Super Body **********
  .super-body {
    font-family: $poppins;
    font-size: 1.25rem;
    line-height: 2rem;
    font-weight: 300;
    border-top: 1px solid $mediumGrey;
    border-bottom: 1px solid $mediumGrey;
    padding: 4rem 0;

    strong, b {
        font-family: $poppins;
        font-weight: 500;
        color: $darkGrey !important;
    }
  }

  // ********** Service **********
  .service-title {
    h2 {
      font-family: $poppins;
      font-weight: 600;
      font-size: 1rem;
      color: $darkGrey;
      text-transform: uppercase;
      margin: 3rem 0;
    }
  }

  .service {
    background: $lightGreige;
    position: relative;
    border-top-left-radius: 5.625rem;
    border-bottom-left-radius: 5.625rem;
    padding: 0.75rem 0;
    z-index: 1;
    margin-top: 0 !important;
    margin-bottom: 4rem;
 
    h3 {
      font-family: $poppins;
      font-size: 1.25rem;
      font-weight: 300;
      line-height: normal;
      color: $darkGrey;
    }
    
    .service-extension {
      background: $lightGreige;
      position: absolute;
      top: 0;
      left: 100%;
      width: 100%;
      height: 100%;
      z-index: 100;
    }

    .service-icon {
      position:relative;

      img {
        height: 10rem;
        width: 80%;
        position: absolute;
        top: -5.2rem;
        right: 0;
        bottom: 0;
        left: 2rem;
      }
    }
  }

  .transformation .service .service-icon img {
    width: 82%;
    height: 8.5rem;
  }

  .software .service .service-icon img {
    width: 64%;
    height: 8.5rem;
    top: -4.5rem;
  }

  .capabilities {
    h3 {
      font-family: $avBlack;
      font-size: 1.25rem;
      color: $primaryBlue;
      padding: .25rem 0 0;

      a {
        color: $primaryBlue;
      }

      :hover {
        color: $primary;
      }
    }

    h4 {
      font-family: $poppins;
      font-weight: 600;
      font-size: 0.85rem !important;
      color: $darkGrey;
      text-transform: uppercase;
      margin-bottom: 1.5rem;
    }

    .description {
      font-family: $avLight;
      font-size: 1.25rem;
      line-height: 1.75rem;
      padding: 0 2.7rem 0 4rem;
    }
  }
 
  .partners {
    border-top: 1px solid $mediumGrey;
    padding-top: 6rem;

    .partners-title {
      h2 {
        font-family: $poppins;
        font-weight: 600;
        font-size: 1rem;
        color: $darkGrey;
        text-transform: uppercase;
        margin-bottom: 2rem;
      }
    }

    .partner-logo {
      img {
        width: 80%
      }  
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575.98px) { 
    .service-title-line {
      margin-left: 1rem;
    }

    .service {
      margin-left: 1rem;

      .service-icon img {
        width: 92%;
        left: 0;
      }
    }

    .transformation .service .service-icon img {
      width: 100%;
    }
  
    .software .service .service-icon img {
      width: 91%;
    }

    .container.partners {
        width: calc(100% - 2rem);
    }


  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767.98px) {
    .capabilities {
      .description, .capability-types {
        padding: 0 4rem;
      }

      .capability-types {
        margin-top: 3rem;
      }
    }
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991.98px) {}

  // Large devices (desktops, less than 1200px)
  @media (max-width: 1199.98px) {}


  // Extra small devices (portrait phones, less than 576px)
  // No media query since this is the default in Bootstrap

  // Small devices (landscape phones, 576px and up)
  @media (min-width: 576px) {}

  // Medium devices (tablets, 768px and up)
  @media (min-width: 768px) {
    .service-card {
      margin: 0 0 10rem;
    }
  }

  // Large devices (desktops, 992px and up)
  @media (min-width: 992px) { }


  #strategy, #transformation, #data, #software {
    margin-top: -20rem;
    padding-bottom: 20rem;
    display: block;
  }

} 