.blog {
   .half-circle-element-blog {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      height: 15rem;
      width: auto;
      transform: scaleX(-1);
   }

   .bg-blue {
       background-color: $primaryBlue;
   }
   
   .bg-half-blue {
       position: relative;
       background-color: $primaryBlue;
       background: linear-gradient(180deg, $primaryBlue 24rem, white 0);
   }

   .blog-header {
      margin:5rem 0 0;
      padding-top:6rem;
      padding-bottom:4rem;
      z-index:10;

      h2 {
         font-family: $poppins;
         color: $greige;
         font-size: 1.25rem;
         text-transform: uppercase;
         padding-left: 1.5rem;
         position: relative;
         top: 2rem;
      }

      .alm-filter--toggle {
         width: 100% !important;
         border-radius: 4rem !important;
         padding: 1.25rem 2rem !important;
         color: $primaryBlue !important;
         font-family: 'Poppins', Arial, Helvetica, sans-serif !important;
         font-weight: 500 !important;
         font-size: 1rem !important;
         border: none !important;
         outline: none !important;
         -webkit-appearance: none !important;
         background: #B8B8B8 !important;
         z-index: 9;
     }
 
      .alm-filters-container {
         margin-bottom:0 !important;
         z-index: 9999;

         .alm-filter--title .alm-filter--toggle:after {
            right: 1.75rem !important;
         }

         .alm-filter--inner {
            padding: 0;
            position: absolute;
            top: 1.5rem !important;
            z-index: 1;
            width: 100%;
         }

         .alm-filter ul {
            padding: 4.5rem 2rem 2.5rem !important;
            margin: 0 0.1rem;
            background: #E3E3E3;
            border-radius: 0 0 2.75rem 2.75rem;
            -webkit-box-shadow: 0 .125rem .25rem rgba(0,0,0,.095) !important;
            box-shadow: 0 .125rem .25rem rgba(0,0,0,.095) !important;

            li {
               margin: 0 0 1rem !important;
            
               div.alm-filter--link {
                  font-size: 1.25rem !important;
                  line-height: 1.55rem;
               }
            }
         }
      
         .alm-filter--title .alm-filter--toggle {
            &:before, &:after {
               border: 1px solid $primaryBlue !important;
               border-top-color: $primaryBlue !important;
               border-width: 0 0.25rem 0.25rem 0 !important;
               display: inline-block !important;
               padding: 0.25rem !important;
               top: 1.3rem !important;
            }

            &:before {
               -webkit-transform: rotate(-45deg) !important;
               transform: rotate(45deg) !important;
            }

            &:after {
               -webkit-transform: rotate(-135deg) !important;
               transform: rotate(45deg) !important;
            }
         }
      }
   }

   .blog-wrapper-background {
      background: $primaryBlue;
      position: absolute;
      height: 37.5rem;
      width: 100%;
   }

   .blog-wrapper {
      padding: 0rem 5rem 2rem;

      &.authors {
         padding: 2rem 0rem 2rem;
      }

      .card {
         min-height: 31rem;
         width: 100%;
         border-radius: 2rem;
         background-color: $greige;
         position: relative;
         display: inline-block;
         transition: 0.4s ease-out;
         border: none !important;
         margin-bottom: 3rem;

         &:hover {
            &:before {
               opacity: 1;
            }

            h2 {
               color: rgba(46, 48, 66, 0.68) !important;
            }

            .info {
               opacity: 1;
               transform: translateY(0px);
            }
         }

         &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 2rem;
            z-index: 2;
            transition: 0.5s;
            opacity: 0;
         }

         .post-image {
            border-radius: 2rem;
            height: 17rem;
            background-size: cover;
            background-repeat: no-repeat;
            background-position:center center;
         }

         .card-body {
            padding: 2rem;

            .category {
               font-family: $poppins;
               font-weight: 500;
               font-size: 1rem;
               color: #4c4c4c;
               text-transform: uppercase;
            }

            .card-title {
               h2 {
                  font-family: $avBlack;
                  color: #2e3041;
                  font-size: 1.25rem;
                  line-height: 1.75rem;
                  padding: 1rem 0 0;
                  margin-bottom: 0;
               }
            }
            

            .card-text {
               font-size: 1.5rem;
               color: $darkGrey !important;
               font-family: $avLight;
            }
         }
      }
   }


   @media (max-width: 575.98px) {
      .blog-header {
         background: $primaryBlue;
         margin: 6em 0 0;
      }

      .blog-wrapper {
         padding: 0rem;
      }

   }
}