                top: 0;
                right: 0;
                width: 50%;
            }
        }

        .right-content {
            .right-content-image, .right-content-image-mobile {      
                position: absolute;
                z-index: 1;
                height: 100%;
                top: 0;
                left: 0;
                width: 50%;
            }
        }
    }

    // Large devices (desktops, 992px and up)
    @media (min-width: 992px) {  }

    // Extra large devices (large desktops, 1200px and up)
    @media (min-width: 1200px) { 
