.wrapper.location {
    padding: 1.25rem 0 5rem;
    // margin-top: 2rem;
}

.location-wrapper {
    position:relative;
    background: rgba(255,255,255,.92);
    border-radius: 3rem 0 0 3rem;
    margin-top: 4rem;

    .location-element {
        position: absolute;
        top: -1.0rem;
        right: 0%;
        width: 10rem;
    }

    .location-wrapper-wrapper-extension {
       position: absolute;
       width: 4rem;
       height: 100%;
       background: rgba(255,255,255,.92);
       top: 0;
       left: 100%;
       z-index: -1;
    }

    .location-address {
        font-family: $avLight;
        color: $darkGrey;
        font-size: 1.25rem;
        line-height: 2rem;
        padding: 2rem 1rem 2rem 2.25rem;

        a {
          color: $darkGrey;
        }

        a:hover {
          color: $primary;
          text-decoration: underline;
        }

        p {
          margin-top: 0;
          margin-bottom: 0rem;
        }

        .email {
          
          a {
            color: $primary;
          }
          a:hover {
            text-decoration: underline;
          }
        }
    }
}

.intro-copy {
  border-top: 1px solid $grey;
  border-bottom: 1px solid $grey;
  margin-top: 1rem;
  padding: 2.5rem 0;

    h2, p {
      font-family: $poppins;
      font-size: 1.4rem;
      line-height: 2rem;
      font-weight: 300;
    }

    * {
      margin-bottom:0;
    }
}

.location-subtitles {
  h2 {
    font-family: $poppins;
    font-weight: 600;
    font-size: 1rem;
    color: $darkGrey;
   
  }
}

.half-circle-element-location {
  transform: scaleX(-1);
  position: absolute;
  top: 720px;
  right: 100px;
  float: right;
  width: 300px;
  z-index: 1;
}

.partner-logos {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.hq-location {
  position: absolute;
  top: 1.3rem;
  right: 1.5rem;
  border-radius: 12.5rem;
  width: 2.5rem;
  height: 2.5rem;
  background-color: white;
  color: $primary;
  font-family: $avHeavy;
  text-align: center;
  line-height: 2.5rem;
  font-size: 1rem;
}

.partner-image {
  // text-align: center;

  img {
    max-height:5rem;
  }
}


// ********** Mobile CSS Views **********

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .location-wrapper {
    margin-top: 4rem;
  }

  .hq-location { 
    top: 1.0rem;
    right: 1.1rem;
  }

  .location .section-margin.careers {
    margin-top:0;
  }

  .location .section-margin {
    margin-top:80px;
    margin-bottom:80px;
  }
}

