.full-circle-element-work {
    position: absolute;
    top: 0;
    right: -9rem;
    z-index: 1;
    height:300%;
    width:auto;
}

.work-header {
    margin:5rem 0;

    .alm-filter--toggle {
        width: 100% !important;
        border-radius: 4rem !important;
        padding: 1.25rem 2rem !important;
        color: #FFFFFF !important;
        font-family: 'Poppins', Arial, Helvetica, sans-serif !important;
        font-weight: 500 !important;
        font-size: 1rem !important;
        border: none !important;
        outline: none !important;
        -webkit-appearance: none !important;
        background: $primaryBlue !important;
        z-index: 9;
    }

    .alm-filters-container {
        margin-bottom:0 !important;
        z-index: 9999;

        .alm-filter--title .alm-filter--toggle:after {
            right: 1.75rem !important;
        }

        .alm-filter--inner {
            padding: 0;
            position: absolute;
            top: 1.5rem !important; 
            z-index: 1;
            width: 100%;
        }

        .alm-filter ul {
            padding: 4.5rem 2rem 2.5rem !important;
            margin: 0 0.1rem;
            background: #E3E3E3;
            border-radius: 0 0 2.75rem 2.75rem;
            -webkit-box-shadow: 0 .125rem .25rem rgba(0,0,0,.095) !important;
            box-shadow: 0 .125rem .25rem rgba(0,0,0,.095) !important;

            li {
                margin: 0 0 1rem !important;
            
                div.alm-filter--link {
                    font-size: 1.25rem !important;
                    line-height: 1.55rem;
                }
            }
        }
      
        .alm-filter--title .alm-filter--toggle {
            &:before, &:after {
                border: 1px solid #ffffff !important;
                border-top-color: #ffffff !important;
                border-width: 0 0.25rem 0.25rem 0 !important;
                display: inline-block !important;
                padding: 0.25rem !important;
                top: 1.3rem !important;
            }

            &:before {
                -webkit-transform: rotate(-45deg) !important;
                transform: rotate(45deg) !important;
            }

            &:after {
                -webkit-transform: rotate(-135deg) !important;
                transform: rotate(45deg) !important;
            }
        }
    }
}

.work-wrapper {
    padding: 0;

    // Load More Button For posts.  Located on index.php
    .misha_loadmore {
       margin: 0 auto 2rem;
    }
}

.work-card {
    width: 100%;
    position: relative;
    display:inline-block;
    transition: 0.4s ease-out;
    padding: 0 0 5rem;
    margin-bottom: 2.5rem;
    border-bottom: 1px solid #979797 !important;   
     
    h2 {
        font-family: $avBlack;
        font-weight: 800;
        color: $primaryBlue;
        font-size: 1.4rem;
        padding: .5rem 1rem 0;
        text-transform: capitalize;
    }
 
    :hover {
        text-decoration: none;
        color: rgba(46,48,66, .68) !important;
    }
     
    .work-image {
        width: 100%;
        min-height:13.75rem;
        height: auto;
        background-size: cover!important;
        background-position: center!important;
        border-radius: 2rem;
    }

    img {
        border-radius: 2rem;
        min-height: 9rem;
        height: auto;
        width: 100%;
        opacity: 1;
        -webkit-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
    }
     
 
    .category {
        font-family: $avLight;
        color: $primaryBlue;
        font-size: 1.2rem;
        font-weight: 500;
        padding: .5rem 1rem 0;
    }
 }


 // Extra small devices (portrait phones, less than 570.5rem)
@media (max-width: 575.98px) {
    .work-header {
        padding: 2rem 0em 4rem;
        margin: 6rem 0 0;
    }

    .work-wrapper {
        padding: 0rem;
        
        #ajax-load-more .container {
            width: calc(100% - 2rem);
        }
    }

    .work-card {
        padding: 0 0 2.5rem;
        margin-bottom: 2.5rem;

        h2 {
            padding: 3.5rem 1rem 0;
        }         
        .work-image {
            width: 95%;
            min-height:13.75rem;
            height: auto;
            background-size: cover!important;
            background-position: center!important;
            border-radius: 3rem;
            margin: 0 auto;
        }
    }
}

